	module.exports = {"data":{"site":{"siteMetadata":{"title":"NathanShane.me"}},"markdownRemark":{"html":"<p>JumpStart Georgia requested data about the national Georgian budget from the Georgian government in 2016. We received spreadsheets and PDFs containing valuable, but disconnected, financial data.</p>\n<p>As the sole developer on this project, I was challenged to organize the financial data and create a website visualizing the data. I chose to separate the frontend, for which I used React/Redux, from the backend, which uses Rails.</p>\n<p>If you're interested in contributing, here are the relevant repositories:</p>\n<ul>\n<li><a href=\"https://github.com/JumpStartGeorgia/Georgian-Budget-API\">Backend/API</a></li>\n<li><a href=\"https://github.com/JumpStartGeorgia/Georgian-Budget-Public\">Public Site</a></li>\n<li><a href=\"https://github.com/JumpStartGeorgia/Georgian-Budget-Files\">Files</a></li>\n</ul>","frontmatter":{"path":"/projects/georgian_budget","title":"Georgian Budget","date":"2017-01-01 14:00:00","tags":"open-source website jumpstart-georgia ruby rails javascript react","mainUrl":"https://biujeti.jumpstart.ge","githubUrl":"https://github.com/JumpStartGeorgia/Georgian-Budget-Public"}}},"pathContext":{"path":"/projects/georgian_budget"}}

